@charset "utf-8";

h2 {text-align: center;}

form {width: 70%; margin: 0 auto; padding: 10px;}
input[type=text], input[type=number] {padding: 10px 20px; width: 70%;}
label {display: inline-block; width: 100px; text-align: right; margin-right: 10px;}
input[type=button] {width: 100%; background-color: rgb(0, 124, 193); border: none; color: #fff; padding: 7px 40px; font-size: 18px;}

.list {display: flex; justify-content: space-between; flex-wrap: wrap;}
.product_list {width: calc(90%/2); padding: 15px;}
.product_list div {width: 100%; margin: 0 auto;}
.product_list div > img {width: 100%;}
.price_text {color: #f00; font-weight: bold; text-align: right;}